.fruit-card {
    background: linear-gradient(#dfe1f7, #e4e5f1);
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    padding: 5px;
    border-radius: 8px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
    cursor: pointer;
}

img {
    max-height: 100%;
    width: 100%;
}

.card-container {
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
}

@media (min-width: 1100px) {
    .card-container {
        padding-left: 100px;
        padding-right: 100px;
        width: 1100px;
    }
}
.wrapper{
    background-color: #a3a9e7;
}
.nav-item {
    text-align: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-family: 'McLaren', cursive;
}

h1, h2, h3, h4, p {
    font-family: 'McLaren', cursive;
}
.mynav{
    position: fixed; /* Set the navbar to fixed position */
    top: 0;
    width: 100%;
    background-color: #6f76c2;
    vertical-align:bottom;
    color: rgb(229, 233, 156);
    text-shadow: 2px 2px 4px #3b3d3b;
}
ul{
    align-items: center
}
.watermelon{
    color:rgb(248, 194, 242);
}
.jumbotron{
    padding-top: 10rem;
    background-color:#e4e5f1;
    color: #6f76c2;
    text-align: center;
}

.modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background: rgba(189, 189, 189, 0.5);
    margin-top: 5rem;
}

.modal-content {
    background-color: #f0efd1;
    border: 2px solid rgb(94, 119, 111);
    margin: 25% auto;
    margin-top: 70px;
    padding: 20px;
    width: 50%;
    text-align: center;
    display: flex;
    justify-content: center;
}

.modaltext {
    font-family: 'Quicksand', sans-serif;
    color: rgb(40, 61, 54);
    text-shadow: none;
    font-size: 20px;
    margin-bottom: 10px;
}

.close {
    text-align: right;
    color: rgb(40, 61, 54);
    opacity: 1;
    text-shadow: none;
    font-size: 28px;
    font-weight: bold;
    margin-top: -15px;
    margin-right: -10px;
}

.close:hover, .close:focus {
    color: rgb(40, 61, 54);
    opacity: 0.5;
    text-decoration: none;
    cursor: pointer;
}

